* {
    background-color: #1C1E1E;
    max-width: 900px;
}

p {
    margin: 0px;
}


/* Header CSS */
.roughin-logo-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-items: center; */
}

.img-div {
    position: relative;
    margin-top: 32px;
    display: flex;
    justify-content: center;
}

.roughin-logo {
    position: relative;
    width: 50px;
    height: 75px;
    
}

.nav {
    position: relative;
    margin-top: 32px;
    display: flex;
    justify-content: center;
    text-align: center;


    font-family: 'Yoon YMjO 540_TT';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 0.25px;
}

.nav-tab-active{
    position: relative;
    margin-right: 16px;;
    color: #FFFFFF;
}

.nav-tab-inactive{
    position: relative;
    margin-right: 16px;;
    color: #666666;
}


/* Body CSS */
.menu-detail-subContainer {
    margin: 40px 1em 0px 1em;
    display: flex;
    justify-content: center;
}

.menu-detail {
    position: relative;
    flex-direction: column;
    margin: 0 0.5em 0 0.5em;
    width: 170px;
    height: fit-content;
}

.menu-detail-title {
    width: 170px;
    margin-top: 4px;
    font-family: 'Yoon YMjO 550_TT';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    letter-spacing: -0.25px;
    color: white;
}

.menu-detail-price {
    width: 170px;
    margin-top: 4px;
    font-family: 'Yoon YMjO 540_TT';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    letter-spacing: 0.25px;
    color: #FFFFFF;
}

.menu-detail-desc {
    width: 170px;
    margin-top: 4px;
    font-family: 'Yoon YMjO 540_TT';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    letter-spacing: 0.25px;
    color: #FFFFFF;
}


/* Footer CSS */
.footer-sns-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: fit-content;
    margin: 60px 0 40px 0;
}

.footer-insta-icon {
    width: 12px;
    height: 12px;
    margin-right: 8px;
}

.footer-insta-id {
    font-family: 'Gadugi';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    /* identical to box height, or 133% */

    letter-spacing: 0.04em;

    color: #FFFFFF;
}

.copyright {
    margin-bottom: 40px;
    font-family: 'Yoon YMjO 540_TT';
    font-style: normal;
    font-weight: 400;
    font-size: 8px;
    line-height: 12px;
    /* identical to box height, or 150% */

    text-align: center;
    letter-spacing: 0.25px;

    /* white */

    color: #666666;
}